import { setDate } from "date-fns";
import React, { useState } from "react";
import DayPickerInput from "react-day-picker/DayPickerInput";
import "react-day-picker/lib/style.css";
import dateFnsFormat from "date-fns/format";

const FORMAT = "dd/MM/yyyy";
function formatDate(date, format, locale) {
  return dateFnsFormat(date, format, { locale });
}

const AddTasks = ({ onCancel, onAddTask }) => {
  const [task, settask] = useState("");
  const [date, setdate] = useState(null);
  return (
    <div className="task-dialogue">
      <input className="tskinp" value={task} onChange={(event) => settask(event.target.value)} />
      <div className="task-btn-action-container">
        <div className="btn-task-action">
          <button
          disabled={!task}
            className="Add-btn"
            onClick={() => {
              onAddTask(task, date);
              onCancel();
              settask("");
            }}
          >
            Add task
          </button>
          <button
            className="cancel-btn"
            onClick={() => {
              onCancel();
              settask("");
            }}
          >
            cancel
          </button>
        </div>
        <div className="btn-calender">
          <DayPickerInput
            onDayChange={(day) => setdate(day)}
            formatDate={formatDate}
            format={FORMAT}
            dayPickerProps={{
              modifiers: {
                disabled: [{ before: new Date() }],
              },
            }}
            placeholder={dateFnsFormat(new Date(), FORMAT)}
          />
        </div>
      </div>
    </div>
  );
};

const TASK_HEADER = {
    All: "All",
    Today: "Today",
    Week: "Week",
}

const Tasks = ({selectedTab}) => {
  const [showAddTask, setshowAddTask] = useState(false);
  const [tasks, settasks] = useState([]);

  const addNewTask = (text, date) => {
    const newTaskAdd = { text, date: date || new Date() };
    settasks((pervState) => [...pervState, newTaskAdd]);
  };
  return (
    <div className="tasks">
      <h1>{TASK_HEADER[selectedTab]}</h1>
      <div
        className="add-task-btn"
        onClick={() => setshowAddTask((pervState) => !pervState)}
      >
        <span className="plus">+</span>
        <span className="add-task-text">Add task</span>
      </div>
      {showAddTask && (
        <AddTasks
          onAddTask={addNewTask}
          onCancel={() => setshowAddTask(false)}
        />
      )}
      {tasks.length > 0 ? (
        tasks.map((task) => (
          <p>
            {task.text}
            {dateFnsFormat(new Date(task.date), FORMAT)}
          </p>
        ))
      ) : 
        <p>No Task added</p>
      }
    </div>
  );
};

export default Tasks;
